"use client";

import { useState, useEffect } from "react";
import { supabase } from "@/lib/supabase";
import { useRouter } from "next/navigation";
import { X, Loader2, Trash2, Plus, Lock, Crown } from "lucide-react";

interface AddProductModalProps {
  storeId: string;
  isOpen: boolean;
  onClose: () => void;
  onSuccess: () => void;
}

export default function AddProductModal({ storeId, isOpen, onClose, onSuccess }: AddProductModalProps) {
  const router = useRouter();
  const [loading, setLoading] = useState(false);
  const [categories, setCategories] = useState<{ id: string; name: string }[]>([]);
  
  // --- LIMIT STATE ---
  const [isLimitReached, setIsLimitReached] = useState(false);
  const [isExpired, setIsExpired] = useState(false);
  
  // --- FORM STATE ---
  const [formData, setFormData] = useState({
    name: "",
    price: "",
    stock: "1",
    description: "",
    categoryId: "",
  });
  
  const [imageFiles, setImageFiles] = useState<File[]>([]);
  const [previews, setPreviews] = useState<string[]>([]);

  // 1. Fetch Data & Check Limits when modal opens
  useEffect(() => {
    if (isOpen) {
      const loadData = async () => {
        // A. Get Categories
        const { data: cats } = await supabase.from("categories").select("*").eq("store_id", storeId);
        setCategories(cats || []);

        // B. Get Store Plan & Expiry
        const { data: store } = await supabase
          .from("stores")
          .select("subscription_plan, subscription_expiry")
          .eq("id", storeId)
          .single();
        
        const plan = store?.subscription_plan || 'free';
        const expiry = store?.subscription_expiry;

        // Check Expiry
        if (plan !== 'free' && expiry && new Date(expiry) < new Date()) {
           setIsExpired(true);
           return; 
        } else {
           setIsExpired(false);
        }

        // C. Get Product Count
        const { count } = await supabase
          .from("products")
          .select("*", { count: 'exact', head: true }) 
          .eq("store_id", storeId);
        
        const currentCount = count || 0;

        // D. Check Free Limit (5 Products)
        if (plan === 'free' && currentCount >= 5) {
          setIsLimitReached(true);
        } else {
          setIsLimitReached(false);
        }
      };
      
      loadData();
    }
  }, [isOpen, storeId]);

  // Handle closing properly
  if (!isOpen) return null;

  // --- IMAGE HANDLING ---
  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const newFiles = Array.from(e.target.files);
      const combinedFiles = [...imageFiles, ...newFiles].slice(0, 4); // Max 4
      setImageFiles(combinedFiles);
      setPreviews(combinedFiles.map(file => URL.createObjectURL(file)));
    }
  };

  const removeImage = (index: number) => {
    const newFiles = imageFiles.filter((_, i) => i !== index);
    setImageFiles(newFiles);
    setPreviews(newFiles.map(file => URL.createObjectURL(file)));
  };

  // --- SUBMISSION ---
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (isLimitReached || isExpired) return; 

    setLoading(true);

    try {
      // 1. Upload Images
      const uploadedUrls: string[] = [];
      for (const file of imageFiles) {
        const fileName = `${storeId}/${Date.now()}-${Math.random().toString(36).substring(7)}`;
        const { error } = await supabase.storage.from("products").upload(fileName, file);
        if (error) throw error;
        
        const { data } = supabase.storage.from("products").getPublicUrl(fileName);
        uploadedUrls.push(data.publicUrl);
      }

      // 2. Insert Product
      const { error: dbError } = await supabase.from("products").insert({
        store_id: storeId,
        name: formData.name,
        price: parseFloat(formData.price),
        stock_quantity: parseInt(formData.stock),
        description: formData.description,
        category_id: formData.categoryId || null,
        image_urls: uploadedUrls, // Using the new Array column
        is_active: true
      });

      if (dbError) throw dbError;

      // 3. Success
      onSuccess();
      onClose();
      
      // Reset Form
      setFormData({ name: "", price: "", stock: "1", description: "", categoryId: "" });
      setImageFiles([]);
      setPreviews([]);

    } catch (error: any) {
      alert("Error: " + error.message);
    } finally {
      setLoading(false);
    }
  };

  // --- RENDER ---
  return (
    <div className="fixed inset-0 z-50 flex items-end sm:items-center justify-center bg-black/60 backdrop-blur-sm p-4 animate-in fade-in">
      <div className="bg-white w-full max-w-md rounded-3xl overflow-hidden shadow-2xl animate-in slide-in-from-bottom-10 flex flex-col max-h-[90vh]">
        
        {/* Header */}
        <div className="p-5 border-b border-gray-100 flex justify-between items-center bg-gray-50">
          <h2 className="font-bold text-lg text-gray-900">Add Product</h2>
          <button onClick={onClose} className="p-2 bg-white rounded-full shadow-sm text-gray-500 hover:bg-gray-100 transition"><X size={20} /></button>
        </div>

        {/* --- STATE 1: EXPIRED PLAN --- */}
        {isExpired ? (
           <div className="p-8 text-center flex flex-col items-center justify-center h-full">
              <div className="w-16 h-16 bg-red-50 rounded-full flex items-center justify-center mb-4">
                 <Lock size={32} className="text-red-400" />
              </div>
              <h3 className="text-xl font-bold text-gray-900 mb-2">Subscription Expired</h3>
              <p className="text-gray-500 text-sm mb-6 text-center max-w-xs">
                Your plan has expired. Please renew to add more products and unhide your store.
              </p>
              <button onClick={() => router.push("/dashboard/subscription")} className="w-full bg-red-600 text-white py-4 rounded-xl font-bold shadow-lg flex items-center justify-center gap-2 hover:bg-red-700 transition">
                Renew Subscription
              </button>
           </div>

        /* --- STATE 2: FREE LIMIT REACHED --- */
        ) : isLimitReached ? (
          <div className="p-8 text-center flex flex-col items-center justify-center h-full">
             <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mb-4">
                <Crown size={32} className="text-yellow-500 fill-yellow-500" />
             </div>
             <h3 className="text-xl font-bold text-gray-900 mb-2">Free Limit Reached</h3>
             <p className="text-gray-500 text-sm mb-6 text-center max-w-xs">
               You have used your 5 free products. Upgrade to <b>Premium</b> to upload unlimited items and get verified.
             </p>
             <button onClick={() => router.push("/dashboard/subscription")} className="w-full bg-gradient-to-r from-gray-900 to-gray-800 text-white py-4 rounded-xl font-bold shadow-lg flex items-center justify-center gap-2 hover:scale-[1.02] transition">
               <Crown size={20} className="text-yellow-400" /> Upgrade to Premium
             </button>
          </div>

        /* --- STATE 3: FORM (ALLOWED) --- */
        ) : (
          <div className="p-6 overflow-y-auto">
            <form onSubmit={handleSubmit} className="space-y-4">
              
              {/* Image Grid */}
              <div>
                <label className="block text-sm font-bold text-gray-700 mb-2">Images (Max 4)</label>
                <div className="grid grid-cols-4 gap-2">
                  {previews.map((src, index) => (
                    <div key={index} className="aspect-square relative rounded-xl overflow-hidden border border-gray-200">
                      <img src={src} alt="Preview" className="w-full h-full object-cover" />
                      <button type="button" onClick={() => removeImage(index)} className="absolute top-1 right-1 bg-red-500 text-white p-1 rounded-full hover:bg-red-600"><Trash2 size={12} /></button>
                    </div>
                  ))}
                  {previews.length < 4 && (
                    <label className="aspect-square rounded-xl border-2 border-dashed border-gray-300 flex flex-col items-center justify-center text-gray-400 bg-gray-50 cursor-pointer hover:border-gray-900 hover:text-gray-900 transition">
                      <Plus size={24} className="mb-1"/>
                      <input type="file" accept="image/*" multiple onChange={handleFileChange} className="hidden" />
                    </label>
                  )}
                </div>
              </div>

              {/* Fields */}
              <div>
                <label className="block text-sm font-bold text-gray-700 mb-1">Product Name</label>
                <input required className="w-full p-3 bg-gray-50 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-gray-900" placeholder="e.g. Nike Air Force" value={formData.name} onChange={e => setFormData({...formData, name: e.target.value})} />
              </div>

              <div className="grid grid-cols-2 gap-4">
                 <div>
                    <label className="block text-sm font-bold text-gray-700 mb-1">Price (₦)</label>
                    <input required type="number" className="w-full p-3 bg-gray-50 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-gray-900" placeholder="50000" value={formData.price} onChange={e => setFormData({...formData, price: e.target.value})} />
                 </div>
                 <div>
                    <label className="block text-sm font-bold text-gray-700 mb-1">Stock Qty</label>
                    <input required type="number" className="w-full p-3 bg-gray-50 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-gray-900" placeholder="10" value={formData.stock} onChange={e => setFormData({...formData, stock: e.target.value})} />
                 </div>
              </div>

              {/* Category Select */}
              <div>
                <label className="block text-sm font-bold text-gray-700 mb-1">Category</label>
                <select 
                  className="w-full p-3 bg-gray-50 border border-gray-200 rounded-xl outline-none focus:ring-2 focus:ring-gray-900"
                  value={formData.categoryId}
                  onChange={e => setFormData({...formData, categoryId: e.target.value})}
                >
                  <option value="">No Category</option>
                  {categories.map(cat => (
                    <option key={cat.id} value={cat.id}>{cat.name}</option>
                  ))}
                </select>
              </div>

              <div>
                <div className="flex justify-between items-center mb-1">
                   <label className="block text-sm font-bold text-gray-700">Description</label>
                   <span className={`text-xs ${formData.description.length > 450 ? 'text-red-500' : 'text-gray-400'}`}>{formData.description.length}/500</span>
                </div>
                <textarea maxLength={500} className="w-full p-3 bg-gray-50 border border-gray-200 rounded-xl h-24 resize-none focus:outline-none focus:ring-2 focus:ring-gray-900" placeholder="Describe the item..." value={formData.description} onChange={e => setFormData({...formData, description: e.target.value})} />
              </div>

              <button type="submit" disabled={loading} className="w-full bg-gray-900 text-white py-4 rounded-xl font-bold shadow-lg hover:bg-gray-800 active:scale-95 transition">
                {loading ? <Loader2 className="animate-spin" /> : "Save Product"}
              </button>
            </form>
          </div>
        )}
      </div>
    </div>
  );
}