"use client";

import { useState } from "react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { 
  LayoutDashboard, Package, ShoppingBag, Settings, LogOut, 
  Plus, ExternalLink, Crown, AlertTriangle, Eye, TrendingUp, Copy, Check
} from "lucide-react";
import { supabase } from "@/lib/supabase";
import AddProductModal from "@/components/store/AddProductModal";

interface DashboardClientProps {
  store: any;
  initialProducts: any[];
  initialOrders: any[];
  stats: { revenue: number; productCount: number; views: number };
}

export default function DashboardClient({ store, initialProducts, initialOrders, stats }: DashboardClientProps) {
  const router = useRouter();
  const [activeTab, setActiveTab] = useState("products");
  const [isAddModalOpen, setIsAddModalOpen] = useState(false);
  const [copied, setCopied] = useState(false);

  // --- SUBSCRIPTION LOGIC ---
  const isPremium = store.subscription_plan === 'premium';
  const isDiamond = store.subscription_plan === 'diamond';
  const isFree = store.subscription_plan === 'free';
  
  // Check Expiry
  const isExpired = store.subscription_expiry && new Date(store.subscription_expiry) < new Date();
  
  // Free Limit Check
  const isFreeLimitReached = isFree && stats.productCount >= 5;

  const handleCopyLink = () => {
    navigator.clipboard.writeText(`${window.location.origin}/${store.slug}`);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  const handleSignOut = async () => {
    await supabase.auth.signOut();
    router.push("/login");
  };

  return (
    <div className="min-h-screen bg-gray-50 flex flex-col md:flex-row font-sans">
      
      {/* --- SIDEBAR --- */}
      <aside className="w-full md:w-64 bg-white border-r border-gray-200 flex-shrink-0">
        <div className="p-6">
          <Link href="/" className="font-extrabold text-xl tracking-tight text-gray-900 flex items-center gap-2">
            <LayoutDashboard className="text-emerald-600"/> StoreLink
          </Link>
        </div>
        
        <nav className="px-4 space-y-1">
          <button onClick={() => setActiveTab("products")} className={`w-full flex items-center gap-3 px-4 py-3 rounded-xl text-sm font-bold transition ${activeTab === "products" ? "bg-gray-100 text-gray-900" : "text-gray-500 hover:bg-gray-50"}`}>
            <Package size={18} /> Products
          </button>
          <button onClick={() => setActiveTab("orders")} className={`w-full flex items-center gap-3 px-4 py-3 rounded-xl text-sm font-bold transition ${activeTab === "orders" ? "bg-gray-100 text-gray-900" : "text-gray-500 hover:bg-gray-50"}`}>
            <ShoppingBag size={18} /> Orders
          </button>
          <Link href="/dashboard/subscription" className={`w-full flex items-center gap-3 px-4 py-3 rounded-xl text-sm font-bold transition text-gray-500 hover:bg-gray-50`}>
            <Crown size={18} className="text-yellow-500" /> Subscription
          </Link>
          <button onClick={handleSignOut} className="w-full flex items-center gap-3 px-4 py-3 rounded-xl text-sm font-bold text-red-500 hover:bg-red-50 transition mt-8">
            <LogOut size={18} /> Sign Out
          </button>
        </nav>

        {/* User Profile Snippet */}
        <div className="absolute bottom-6 left-6 right-6">
           <div className="flex items-center gap-3 p-3 bg-gray-50 rounded-xl border border-gray-100">
              <div className="w-8 h-8 bg-gray-200 rounded-full flex items-center justify-center text-xs font-bold text-gray-500">
                {store.name.charAt(0)}
              </div>
              <div className="overflow-hidden">
                <p className="text-xs font-bold text-gray-900 truncate">{store.name}</p>
                <p className="text-[10px] text-gray-500 capitalize">{store.subscription_plan} Plan</p>
              </div>
           </div>
        </div>
      </aside>

      {/* --- MAIN CONTENT --- */}
      <main className="flex-1 p-4 md:p-8 overflow-y-auto">
        
        {/* HEADER */}
        <header className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4 mb-8">
          <div>
            <h1 className="text-2xl font-bold text-gray-900 flex items-center gap-2">
              Dashboard
              {isDiamond && <span className="bg-purple-100 text-purple-700 text-xs px-2 py-0.5 rounded-md border border-purple-200">Diamond</span>}
              {isPremium && <span className="bg-blue-100 text-blue-700 text-xs px-2 py-0.5 rounded-md border border-blue-200">Premium</span>}
              {isFree && <span className="bg-gray-100 text-gray-600 text-xs px-2 py-0.5 rounded-md border border-gray-200">Free</span>}
            </h1>
            <p className="text-gray-500 text-sm">Welcome back, {store.name}.</p>
          </div>
          
          <div className="flex gap-2">
            <button onClick={handleCopyLink} className="flex items-center gap-2 px-4 py-2 bg-white border border-gray-200 text-gray-700 rounded-xl text-sm font-bold hover:bg-gray-50 transition">
              {copied ? <Check size={16} className="text-green-500"/> : <Copy size={16}/>} 
              {copied ? "Copied!" : "Copy Shop Link"}
            </button>
            <a href={`/${store.slug}`} target="_blank" className="flex items-center gap-2 px-4 py-2 bg-gray-900 text-white rounded-xl text-sm font-bold hover:bg-gray-800 transition">
              <ExternalLink size={16} /> View Store
            </a>
          </div>
        </header>

        {/* --- ALERTS --- */}
        {isExpired && (
          <div className="mb-6 bg-red-50 border border-red-100 p-4 rounded-xl flex items-center justify-between">
             <div className="flex items-center gap-3">
               <AlertTriangle className="text-red-500" />
               <div className="text-sm">
                 <span className="font-bold text-red-900">Plan Expired.</span> Your store is currently hidden. Renew now to get back online.
               </div>
             </div>
             <Link href="/dashboard/subscription" className="px-4 py-2 bg-red-600 text-white text-xs font-bold rounded-lg hover:bg-red-700">Renew</Link>
          </div>
        )}

        {isFreeLimitReached && !isExpired && (
          <div className="mb-6 bg-yellow-50 border border-yellow-100 p-4 rounded-xl flex items-center justify-between">
             <div className="flex items-center gap-3">
               <Crown className="text-yellow-600" />
               <div className="text-sm">
                 <span className="font-bold text-yellow-900">Limit Reached.</span> You've hit the 5-product limit.
               </div>
             </div>
             <Link href="/dashboard/subscription" className="px-4 py-2 bg-yellow-500 text-white text-xs font-bold rounded-lg hover:bg-yellow-600">Upgrade</Link>
          </div>
        )}

        {/* --- STATS CARDS --- */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-8">
          <div className="bg-white p-6 rounded-2xl border border-gray-100 shadow-sm">
             <div className="flex items-center gap-3 mb-2">
               <div className="p-2 bg-emerald-50 text-emerald-600 rounded-lg"><TrendingUp size={20}/></div>
               <span className="text-sm font-bold text-gray-500">Total Revenue</span>
             </div>
             <p className="text-2xl font-extrabold text-gray-900">₦{stats.revenue.toLocaleString()}</p>
          </div>

          <div className="bg-white p-6 rounded-2xl border border-gray-100 shadow-sm">
             <div className="flex items-center gap-3 mb-2">
               <div className="p-2 bg-blue-50 text-blue-600 rounded-lg"><Package size={20}/></div>
               <span className="text-sm font-bold text-gray-500">Products</span>
             </div>
             <div className="flex items-baseline gap-2">
               <p className="text-2xl font-extrabold text-gray-900">{stats.productCount}</p>
               {isFree && <span className="text-xs font-bold text-gray-400">/ 5 Free Limit</span>}
             </div>
          </div>

          <div className="bg-white p-6 rounded-2xl border border-gray-100 shadow-sm">
             <div className="flex items-center gap-3 mb-2">
               <div className="p-2 bg-purple-50 text-purple-600 rounded-lg"><Eye size={20}/></div>
               <span className="text-sm font-bold text-gray-500">Store Views</span>
             </div>
             <p className="text-2xl font-extrabold text-gray-900">{stats.views.toLocaleString()}</p>
          </div>
        </div>

        {/* --- PRODUCTS TAB --- */}
        {activeTab === "products" && (
          <div>
            <div className="flex justify-between items-center mb-6">
              <h3 className="font-bold text-lg text-gray-900">Your Products</h3>
              <button 
                onClick={() => setIsAddModalOpen(true)}
                className="flex items-center gap-2 px-5 py-2.5 bg-gray-900 text-white rounded-xl text-sm font-bold shadow-lg hover:bg-gray-800 transition active:scale-95"
              >
                <Plus size={18}/> Add Product
              </button>
            </div>

            <div className="bg-white border border-gray-100 rounded-2xl overflow-hidden shadow-sm">
               {initialProducts.length === 0 ? (
                 <div className="p-12 text-center text-gray-400">
                   <Package size={48} className="mx-auto mb-4 opacity-20"/>
                   <p>No products yet. Add your first one!</p>
                 </div>
               ) : (
                 <table className="w-full text-left">
                   <thead className="bg-gray-50 border-b border-gray-100 text-xs uppercase text-gray-500">
                     <tr>
                       <th className="px-6 py-4 font-bold">Product</th>
                       <th className="px-6 py-4 font-bold">Category</th>
                       <th className="px-6 py-4 font-bold">Price</th>
                       <th className="px-6 py-4 font-bold">Stock</th>
                       <th className="px-6 py-4 font-bold text-right">Status</th>
                     </tr>
                   </thead>
                   <tbody className="divide-y divide-gray-100">
                     {initialProducts.map((p) => (
                       <tr key={p.id} className="hover:bg-gray-50/50 transition">
                         <td className="px-6 py-4 font-medium text-gray-900">{p.name}</td>
                         <td className="px-6 py-4 text-gray-500 text-sm">{p.categories?.name || '-'}</td>
                         <td className="px-6 py-4 text-gray-900 font-bold text-sm">₦{p.price.toLocaleString()}</td>
                         <td className="px-6 py-4 text-sm">
                           {p.stock_quantity === 0 ? <span className="text-red-500 font-bold">Out</span> : p.stock_quantity}
                         </td>
                         <td className="px-6 py-4 text-right">
                           <span className={`inline-block px-2 py-1 rounded text-xs font-bold ${p.is_active ? 'bg-green-100 text-green-700' : 'bg-gray-100 text-gray-500'}`}>
                             {p.is_active ? 'Active' : 'Draft'}
                           </span>
                         </td>
                       </tr>
                     ))}
                   </tbody>
                 </table>
               )}
            </div>
          </div>
        )}

        {/* --- ORDERS TAB --- */}
        {activeTab === "orders" && (
          <div>
            <h3 className="font-bold text-lg text-gray-900 mb-6">Order History</h3>
            <div className="bg-white border border-gray-100 rounded-2xl overflow-hidden shadow-sm">
              {initialOrders.length === 0 ? (
                 <div className="p-12 text-center text-gray-400">
                   <ShoppingBag size={48} className="mx-auto mb-4 opacity-20"/>
                   <p>No orders yet. Share your link to start selling!</p>
                 </div>
              ) : (
                 <table className="w-full text-left">
                   <thead className="bg-gray-50 border-b border-gray-100 text-xs uppercase text-gray-500">
                     <tr>
                       <th className="px-6 py-4 font-bold">Customer</th>
                       <th className="px-6 py-4 font-bold">Total</th>
                       <th className="px-6 py-4 font-bold">Date</th>
                       <th className="px-6 py-4 font-bold text-right">Status</th>
                     </tr>
                   </thead>
                   <tbody className="divide-y divide-gray-100">
                     {initialOrders.map((order) => (
                       <tr key={order.id} className="hover:bg-gray-50/50 transition">
                         <td className="px-6 py-4">
                           <div className="font-bold text-gray-900 text-sm">{order.customer_name}</div>
                           <div className="text-xs text-gray-500">{order.customer_phone}</div>
                         </td>
                         <td className="px-6 py-4 text-emerald-600 font-bold text-sm">₦{order.total_amount.toLocaleString()}</td>
                         <td className="px-6 py-4 text-gray-500 text-xs">{new Date(order.created_at).toLocaleDateString()}</td>
                         <td className="px-6 py-4 text-right">
                           <span className={`inline-block px-2 py-1 rounded text-xs font-bold capitalize ${
                             order.status === 'completed' ? 'bg-green-100 text-green-700' :
                             order.status === 'cancelled' ? 'bg-red-100 text-red-700' :
                             'bg-yellow-100 text-yellow-700'
                           }`}>
                             {order.status}
                           </span>
                         </td>
                       </tr>
                     ))}
                   </tbody>
                 </table>
              )}
            </div>
          </div>
        )}

      </main>

      {/* --- ADD PRODUCT MODAL --- */}
      <AddProductModal 
        storeId={store.id} 
        isOpen={isAddModalOpen} 
        onClose={() => setIsAddModalOpen(false)} 
        onSuccess={() => router.refresh()} 
      />

    </div>
  );
}